import React from 'react';
import {
    Dropdown,
    DropdownItem,
    DropdownToggle
} from '@patternfly/react-core/deprecated';
import PropTypes from 'prop-types';

interface Props {
    defaultText: string;
    idValue: string;
    items:string[];
    onSelect: Function;
}

/**
 * A subclass to handle all of the bloat that comes with this under patternfly design
 * @param props primarily intended to  pass the array of items provided to it
 * @constructor
 */
const DropdownHandler = ({defaultText, idValue, items, onSelect}: Props) => {
    const [isDDOpen, setIsDDOpen] = React.useState(false);
    const [selection, setSelection] = React.useState(defaultText);

    const onDDToggle = (isOpen:boolean) => {
        setIsDDOpen(isOpen);
    };

    const onDDFocus = () => {
        const element = document.getElementById(idValue);
        element!.focus();
    };
    const onDDSelect = () => {
        setIsDDOpen(false);
        onDDFocus();
    };

    return (
        <Dropdown
            onSelect={onDDSelect}
            toggle={
                <DropdownToggle id={idValue} onToggle={(_event, isOpen) => onDDToggle(isOpen)}>
                    {selection}
                </DropdownToggle>
            }
            isOpen={isDDOpen}
            dropdownItems={items.map((val:string) => {
                return <DropdownItem key={val} onClick={ () => { onSelect(val); setSelection(val); }}>{val}</DropdownItem> })}
        />
    )
}

DropdownHandler.propTypes = {
    defaultText: PropTypes.string,
    idValue: PropTypes.string,
    items: PropTypes.array,
    onSelect: PropTypes.func
}
export default DropdownHandler;
