import React, { useState, useEffect } from 'react';
import UserManagementController from "../../Apps/UserManagement/user-management-controller";
import {isAdminUser} from "../../Utils/general-utils";
import DisabledPage from "../../Components/DisabledPage/disabled-page";
import {useFlag} from '@unleash/proxy-client-react';
import {useChrome} from '@redhat-cloud-services/frontend-components/useChrome';


const UserManagement = () => {
    const chrome = useChrome();
    let userToken = null;
    const [currentUser, updateUser] = useState({
        adminUser: true,
        renderedPage: <UserManagementController />
    })
    const isUserManagementEnabled = useFlag('iamconsole.usermanagement.enabled');

    useEffect(() => {
        // insights?.chrome?.appAction?.('identity-provider-integration');
        // eslint-disable-next-line no-undef
        chrome.auth.getToken().then(token => {
            userToken = token;
            // if the user is in fact and admin user then render TwoFactorCheckOld
            if (isAdminUser(userToken!) && isUserManagementEnabled) {
                updateUser({
                    adminUser: true,
                    renderedPage: <UserManagementController />
                })
            }

            // check to see if 2fa feature flag is set. if not then render the proper error page
            if (!isUserManagementEnabled) {
                updateUser({
                    adminUser: false,
                    renderedPage: <DisabledPage/>
                })
            }

        })
    }, []);


    return (
        <React.Fragment>
            {currentUser.renderedPage}
        </React.Fragment>
    )
}

export default UserManagement;
